*,
*::before,
*::after {
  box-sizing: border-box;
}

ul[class],
ol[class] {
  padding: 0;
  list-style: none;
  margin: 0;
}

/* Убираем внешние отступы */
body,
h1,
h2,
h3,
h4,
p,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

img {
  max-width: 100%;
  display: block;
}

.search-form input {
  margin: 0;
}

.search-form input[type='text'] {
  padding: 6px;
  font-size: 17px;
  border: none;
}

.search-form button {
  float: right;
  padding: 6px;
  background: #ddd;
  font-size: 17px;
  border: none;
  cursor: pointer;
}

.search-form button:hover {
  background: #ccc;
}

.wraper {
  background-color: rgb(123, 123, 196);
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-top: -10px;
  padding: 20px;
}

.photo-card {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  margin-left: 20px;
  margin-top: 20px;
  height: 248px;
  flex-basis: calc((100% - 80px) / 4);
  background-color: #dbd9d9c9;

  border-radius: 4px;
  line-height: 1.5;
  font-family: sans-serif;
  background-color: #fff;

  box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.3);

  overflow: hidden;
}

.info {
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: space-around;
}

.info-item {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.photo-card img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  opacity: 1;
  -webkit-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
}

.photo-card img:hover {
  opacity: 0.5;
}

.photo-card a {
  width: 100%;
  height: 200px;
}

.container {
  padding-bottom: 50px;
  width: 150px;
  margin: 0 auto;
}

.load-more {
  background: rgb(123, 123, 196);
  width: 150px;
  height: 50px;

  border: none;
  color: #f2f2f2;
  padding: 10px;
  font-size: 18px;
  border-radius: 5px;
  position: relative;
  box-sizing: border-box;
  transition: all 500ms ease;
}

.load-more:hover {
  background: rgb(75, 75, 126);
}
